<script lang="ts">
export default {
  name: 'PatientDetail',
}
</script>

<script setup lang="ts">
import { usePatientInfoStore } from '@/stores/patientInfoStore'
import { storeToRefs } from 'pinia'
import { defineAsyncComponent, onMounted, onActivated } from 'vue'
import type { Component } from 'vue'
import { useRoute } from 'vue-router'

const route = useRoute()
const patientInfoStore = usePatientInfoStore()
const { detailTabs, activeTab } = storeToRefs(patientInfoStore)

interface NavItem {
  id: string
  title: string
  component?: Component
  children?: NavItem[]
}

// 默认打开的标签页ID
const defaultOpenTabId = 'patient-basic'
// 导航菜单配置
const navItems: NavItem[] = [
  {
    id: 'basic',
    title: '基本信息',
    children: [
      {
        id: 'patient-basic',
        title: '患者基本信息',
        component: defineAsyncComponent(() => import('./components/PatientBasicInfo.vue')),
      },
      {
        id: 'patient-surgery',
        title: '手术信息',
        component: defineAsyncComponent(() => import('./components/PatientSurgeryInfo.vue')),
      },
    ],
  },
  {
    id: 'nursing',
    title: '护理信息',
    component: defineAsyncComponent(() => import('./components/PatientNursingInfo.vue')),
  },
  {
    id: 'records',
    title: '病历记录',
    component: defineAsyncComponent(() => import('./components/PatientMedicalRecords.vue')),
  },
]

// 根据ID获取导航项
const getNavItemById = (id: string) => {
  const findItem = (items: NavItem[], targetId: string): NavItem | undefined => {
    for (const item of items) {
      if (item.id === targetId) return item
      if (item.children) {
        const found = findItem(item.children, targetId)
        if (found) return found
      }
    }
    return undefined
  }
  return findItem(navItems, id)
}

// 处理导航点击
const handleNavClick = (item: NavItem) => {
  if (item.id) {
    patientInfoStore.addDetailTab(activeTab.value.patientId, item.title, {
      id: item.id,
      title: item.title,
    })
  }
}

// 处理标签页关闭
const handleTabClose = (tabId: string) => {
  patientInfoStore.closeDetailTab(activeTab.value.patientId, tabId)
}

// 初始化或更新患者标签页
const initPatientTabs = () => {
  const patientId = route.params.id as string
  const patientName = route.params.name as string

  console.log('初始化/更新患者标签页:', { patientId, patientName })

  // 如果该患者没有标签页，添加默认标签页
  if (!detailTabs.value[patientId]?.length) {
    const tabInfo = getNavItemById(defaultOpenTabId)
    patientInfoStore.addDetailTab(patientId, patientName, {
      id: tabInfo?.id || '',
      title: tabInfo?.title || '',
    })
  } else {
    patientInfoStore.setActiveTab(patientId, patientName, detailTabs.value[patientId][0].id)
  }
}

// 监听路由参数变化
// watch(
//   () => route.params,
//   () => {
//     initPatientTabs()
//   },
// )

onMounted(() => {
  initPatientTabs()
})

onActivated(() => {
  console.log('组件激活，更新患者信息')
  initPatientTabs()
})
</script>

<template>
  <div class="patient-detail">
    <!-- 左侧导航 -->
    <div class="nav-sidebar">
      <!-- :default-openeds="[defaultOpenTabId]" -->
      <el-menu class="nav-menu">
        <template v-for="item in navItems" :key="item.id">
          <!-- 带子菜单的导航项 -->
          <template v-if="item.children?.length">
            <el-sub-menu :index="item.id">
              <template #title>
                <span>{{ item.title }}</span>
              </template>
              <el-menu-item
                v-for="child in item.children"
                :key="child.id"
                :index="child.id"
                @click="handleNavClick(child)"
              >
                {{ child.title }}
              </el-menu-item>
            </el-sub-menu>
          </template>
          <!-- 普通导航项 -->
          <template v-else>
            <el-menu-item :index="item.id" @click="handleNavClick(item)">
              {{ item.title }}
            </el-menu-item>
          </template>
        </template>
      </el-menu>
    </div>

    <!-- 右侧内容区 -->
    <div class="content-area">
      <!-- 标签页导航 -->
      <div class="tabs-container" v-if="detailTabs[activeTab.patientId]?.length > 0">
        <el-tabs
          v-model="activeTab.tabId"
          type="card"
          closable
          @tab-remove="handleTabClose"
          class="detail-tabs"
        >
          <el-tab-pane
            v-for="tab in detailTabs[activeTab.patientId]"
            :key="tab.id"
            :label="tab.title"
            :name="tab.id"
          >
            <component
              :is="getNavItemById(tab.id)?.component"
              :patient-id="activeTab.patientId"
              :tab-type="tab.id"
            />
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
  </div>
</template>

